@keyframes circle {
  from {
    transform: rotate(90deg);
  }

  to {
    transform: rotate(450deg);
  }
}

body {
  margin: 0;
}

.wrapper {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__minute {
  transform-origin: 100%;
  background: #0700ff;
  width: 20vmin;
  height: 3vmin;
  animation: 3600s steps(60, end) infinite circle;
  position: absolute;
  right: 50%;
  transform: rotate(90deg);
}

.stopwatch__second {
  transform-origin: 100%;
  background: #2c8000;
  width: 38vmin;
  height: 1.5vmin;
  animation: 60s linear infinite circle;
  position: absolute;
  right: 50%;
  transform: rotate(90deg);
}

.stopwatch__center {
  background: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch--speed-up .stopwatch__minute {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__second {
  animation-duration: 10s;
}
